@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,700;1,900&display=swap');


:root{
    --font-family: 'Montserrat', sans-serif;

    --color-primary: #154b63;
    --color-secondary: rgb(43, 44, 45);
    --color-tertiary: rgb(145, 182, 222);
    --color-text: rgb(96, 117, 139);
    --color-bg: rgb(243, 243, 243);
}

  

  
  ::-webkit-scrollbar {
    width: 6px !important;
    height: 6px !important;
  }
  
  body ::-webkit-scrollbar-thumb {
    background-color: hsla(0, 0%, 100%, 0.16);
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2);
  }
  
  ::-webkit-scrollbar-track {
    background: hsla(0, 0%, 100%, 0.1);
  }
  
  .btn {
    border-radius: 50px;
    padding: 14px 0;
    width: 160px;
    cursor: pointer;
    transition: 0.2s;
    font-family: "Poppins SemiBold";
  }
  
  .btn:active {
    transform: translateY(2px);
  }
  
  .primary-btn {
    color: var(--white);
    border: 2px solid linen;
    font-size: 12px;
    background-color: #1f2235;
  }
  /* @media only screen and (max-width: 414px) {
    .primary-btn {
      font-size: 13px;
    }
  } */
  
  .primary-btn:hover {
    color: aliceblue;
    border: 2px solid #ff5823;
    background-color: #1f2235;
  }
  
  .highlighted-btn {
    color: var(--white);
  
    font-size: 12px;
    background-color: #ff5823;
  }
  @media only screen and (max-width: 568px) {
    .highlighted-btn {
      z-index: -900;
    }
  }
  
  .red-border {
    border: 1px solid var(--error-message) !important;
  }
  
  .fade-in {
    opacity: 0;
    transform: translateY(80px);
    transition: all 0.8s ease;
  }
  